import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import {
  postData,
  postDataSaleForce,
  postDataWithoutToken,
} from "../config/FetchNodeServices";
import { checkEmail, checkRequire } from "../config/Checks";
import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import Checkbox from "@mui/material/Checkbox";
import swal from "sweetalert";

const useStyles = makeStyles((theme) => ({
  mainroot: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    // alignItems: 'center'
  },

  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
    fontSize: 32,
  },
  image: {
    display: "flex",
    maxHeight: "100vh",
    maxWidth: "100vh",
  },
  default: {
    marginLeft: "15%",
    marginRight: "15%",
  },
}));

const label = { inputProps: { "aria-label": "Checkbox demo" } };

export default function Login(props) {
  const classes = useStyles();
  const [getEmail, setEmail] = useState("");
  const [loading, setLoading] = useState(true);
  const [getPassword, setPassword] = useState("");
  const [getEmailErr, setEmailErr] = useState("");
  const [getPasswordErr, setPasswordErr] = useState("");
  const [values, setValues] = useState({
    password: "",
    showPassword: false,
  });

  useEffect(() => {
    chkToken();
  }, []);

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      setLoading(false);
    } else {
      props.history.replace({ pathname: "/Dashboard" });
      setLoading(false);
    }
  };

  const style = {
    padding: "300px 0",
    textAlign: "center",
    opacity: 1,
  };

  if (loading) {
    return (
      <div
      // style={{
      //   backgroundImage: `url(${valvo})`,
      //   backgroundSize: "cover",
      // }}
      >
        <div style={style}>
          <CircularProgress color="secondary" />
        </div>
      </div>
    );
  }

  const handleLogin = async (e) => {
    // console.log("HIIIIIIIIIIIIIIIIIII");
    e.preventDefault();
    var err = false;
    if (!checkEmail(getEmail)) {
      err = true;
      setEmailErr("/images/cross.png");
    }
    if (checkEmail(getEmail)) {
      setEmailErr("/images/tick.png");
    }
    if (!checkRequire(getPassword)) {
      err = true;
      setPasswordErr("/images/cross.png");
    }
    if (checkRequire(getPassword)) {
      setPasswordErr("/images/tick.png");
    }
    if (!err) {
      let body = {
        email: getEmail,
        password: getPassword,
      };
      // console.log("BODYYYYYY IN LOGIN", body);
      let LogInResult = await postDataWithoutToken("Login/LoginApi", body);
      console.log("LogInResult======>>>", LogInResult);
      console.log("LogInResult======>>> 115", LogInResult.acountresult[0]);
      console.log(
        "LogInResult===============>>>>>>>>>>>=========>>>",
        LogInResult.final_Resultt
      );
      if (LogInResult.status == false) {
        swal("Email not found", "", "error", {
          buttons: true,
        });
      } else if (
        LogInResult.status &&
        LogInResult.final_Resultt.records[0].Student_Passowrd__c != getPassword
      ) {
        swal("Password is not correct", "", "error", {
          buttons: true,
        });
      } else if (
        LogInResult.final_Resultt.totalSize == 0 &&
        LogInResult.final_Resultt.records
      ) {
        swal("This Email Doesn't Exist", "", "error", {
          buttons: true,
        });
      } else {
        localStorage.setItem(
          "User",
          JSON.stringify({
            // name: LogInResult.final_Resultt.records[0].Name,
            email: LogInResult.final_Resultt.records[0].Email__c,
            id: LogInResult.final_Resultt.records[0].Id,
            password: LogInResult.final_Resultt.records[0].Student_Passowrd__c,
            country: LogInResult.acountresult[0].Country__c,
            // city: LogInResult.acountresult[0].city,
            // state : LogInResult.acountresult[0].state
          })
        );
        localStorage.setItem(
          "token",
          JSON.stringify({ token: LogInResult.token })
        );
        localStorage.setItem(
          "Account",
          JSON.stringify({
            // name: LogInResult.final_Resultt.records[0].Name,
            Name: LogInResult.acountresult[0].Name,
            RefCode: LogInResult.acountresult[0].Referral_Code__c,
          })
        );
        props.history.replace({ pathname: "/Dashboard" });
      }
    } else {
      swal("Please Enter Missing filed to Login", " ", "error", {
        buttons: true,
      });
    }
  };

  return (
    <>
      <div>
        <div className={classes.mainroot}>
          <div
            style={{
              display: "flex",
              // padding: 20,
              // borderRadius: "2%",
              background: "#FFFFFF",
            }}
          >
            <Grid container>
              <Grid item xs={12} sm={6} className={classes.image}>
                <img src="images/LoginLogo.jpeg" />
              </Grid>
              <Grid item xs={12} sm={6} className={classes.mainroot}>
                {/* <form onSubmit={(e) => handleLogin(e)}> */}
                <Grid container spacing={1}>
                  <Grid item xs={12} className={classes.default}>
                    <h3>Log In</h3>
                  </Grid>

                  <Grid item xs={12} className={classes.default}>
                    <span>
                      <b>Email Address</b>
                    </span>
                    <input
                      type="email"
                      class="form-control mt-2 mb-2 bordClr"
                      placeholder="Email"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      onChange={(e) => setEmail(e.target.value)}
                    />
                  </Grid>
                  <Grid item xs={12} className={classes.default}>
                    <span class="mt-2">
                      <b>Password</b>
                    </span>
                    <input
                      type="password"
                      // id="exampleInputPassword1"
                      class="form-control mt-2 bordClr"
                      placeholder="Password"
                      onChange={(e) => setPassword(e.target.value)}
                    />
                  </Grid>

                  <Grid
                    item
                    xs={12}
                    style={{ marginLeft: "18%", marginRight: "15%" }}
                  >
                    <input
                      type="checkbox"
                      class="form-check-input mt-1"
                      id="exampleCheck1"
                    />

                    <label class="form-check-label" for="exampleCheck1">
                      <b>Keep me logged in</b>
                    </label>

                    {/* <span>
                      <a
                        href="#"
                        class="forgot-password"
                        style={{ marginLeft: "40%" }}
                      >
                        Forgot password?
                      </a>
                    </span> */}
                  </Grid>
                  <Grid
                    item
                    xs={12}
                    style={{
                      marginLeft: "15%",
                      marginRight: "15%",
                      // color: "blue",
                    }}
                  >
                    <Button
                      style={{ backgroundColor: "#1e90ff" }}
                      variant="contained"
                      color="primary"
                      fullWidth
                      onClick={handleLogin}
                    >
                      Log In
                    </Button>
                  </Grid>
                  {/* <Grid item xs={12} className={classes.default}>
                    <Button
                      style={{ backgroundColor: "white", color: "black" }}
                      variant="contained"
                      color="primary"
                      fullWidth
                      onClick="#"
                    >
                      <img
                        style={{ width: "5%", height: "5%" }}
                        src="images/icons8-google-48.png"
                      />
                      Log in with Google
                    </Button>
                  </Grid> */}
                  <Grid item xs={12} className={classes.default}>
                    <div>
                      Not a Member?
                      <a
                        href="https://dev1-superstep.cs73.force.com/"
                        // style={{ color: "blue" }}
                      >
                        Sign up
                      </a>
                    </div>
                  </Grid>
                </Grid>
                {/* </form> */}
              </Grid>
            </Grid>
          </div>
        </div>
      </div>
    </>
  );
}

