import React, { useState, useEffect } from "react";
import { welcome, announcements } from "../config/Welcome&AnnouncementsNotes";

import CurrentBatch from "./CurrentBatch";

import { postData, getData } from "../config/FetchNodeServices";
import swal from "sweetalert";

export default function Mainview() {
  const [getList, setList] = useState([]);
  const [getWlcmList, setWlcmList] = useState([]);
  const [getAnnounceList, setAnnounceList] = useState([]);

  const showData = async () => {
    var User = JSON.parse(localStorage.getItem("User"));
    // console.log("BODYYYYYYYYYYY", User);
    // console.log("BODYYYYYYYYYYY DOT EMAIL", User.email);
    let body = {
      email: User.email,
    };
    var list = await postData("Student/CurrentBatch", body);
    if (list) {
      // console.log("LISTTTTT OF CURRENT BATCH", list.Data);
      setList(list.Data);
      swal("Record Found...", " ", "success", {
        button: false,
      });
    } else {
      // console.log("Error", list.error);
      swal("Error Occurred", " ", "error", {
        buttons: false,
      });
    }
  };

  useEffect(() => {
    // showData();
    setWlcmList(welcome);
    setAnnounceList(announcements);
  }, []);

  const WlcmFunction = () => {
    return getWlcmList.map((item) => {
      return (
        <div
          class="col-md-6"
          style={{
            border: "solid 10px #d0f5ec",
            padding: "3%",
            borderRadius: "7px",
          }}
        >
          <h1 style={{ color: "#18a0fb", fontWeight: "bold" }}>{item.title}</h1>

          <span>{item.txt}</span>
        </div>
      );
    });
  };

  const AnnounceFunction = () => {
    return getAnnounceList.map((item) => {
      return (
        <div class="col-md-6" style={{ backgroundColor: "#fbfbfb" }}>
          <div class="m-4">
            <h5>
              <b>{item.title}</b>
            </h5>
            {item.txt.map((item1) => {
              return (
                <>
                  <p style={{ backgroundColor: "#FFF", padding: 5 }}>
                    {item1.name}
                  </p>
                  <p style={{ backgroundColor: "#FFF", padding: 5 }}>
                    {item1.name2}
                  </p>
                </>
              );
            })}
          </div>
        </div>
      );
    });
  };

  return (
    <>
      <div class="card heading">
        <div
          class="row"
          style={{ justifyContent: "space-between", margin: "6px" }}
        >
          {WlcmFunction()}
          {AnnounceFunction()}
        </div>
      </div>
      <div class="col-md-9">
        <CurrentBatch />
      </div>
      {/* <div
        class="card mt-4 heading"
        style={{ backgroundColor: "#ecf0f1", borderRadius: "5px" }}
      >
        <div>
          <b>CURRENT BATCH</b>
        </div>
      </div>
      <div class="card heading">
        <div class="row">
          <div class="col-md-3 titlecolor">BATCH ID</div>
          <div class="col-md-3 ">INDB001</div>
          <div class="col-md-3 ">
            <span>15 May 2021 </span>
          </div>
          <div class="col-md-3 ">One on One</div>
        </div>
      </div>
      <div class="card heading">
        <div class="row">
          <div class="col-md-3 titlecolor">RELATIONSHIP MANAGER </div>
          <div class="col-md-3">Johny Depp</div>
          <div class="col-md-3">+91 0123456789</div>
          <div class="col-md-3">someone@gmail.com</div>
        </div>
      </div>
      <div class="card heading">
        <div class="row">
          <div class="col-md-3 titlecolor">COACH</div>
          <div class="col-md-3">Johny Depp</div>
        </div>
      </div>
      <div class="card heading">
        <div class="row">
          <div class="col-md-3 titlecolor">LEVEL</div>
          <div class="col-md-3">Johny Depp</div>
        </div>
      </div> */}
      {/* </div> */}
    </>
  );
}
