import React, { useState, useEffect } from "react";
import Batches from "./Batches";
import Footer from "./Footer";
import Header from "./Header";
import Mainview from "./Mainview";
import MyCart from "./MyCart";
import Mygames from "./Mygames";
import Myhomeworks from "./Myhomework";
import Myreviews from "./Myreviews";
import Referandearn from "./Referandearn";
import Tournaments from "./Tournaments";
import StudentProfile from "../usericon/StudentProfile";
import PurchaseHistory from "../usericon/PurchaseHistory";
import MyCourse from "../usericon/MyCourse";
import { adminRoutes, StudeMenu, TrainerMenu } from "../config/MenuItems";
import CircularProgress from "@material-ui/core/CircularProgress";
import ClassDuration from "./ClassDuration";
import CheckPayment from "./CheckPayment";

import Notifications from "./Notifications";
import NotiDetails from "./NotiDetails";

import HelpAndSupport from "../usericon/HelpAndSupport";
import ListItems from "../ListItems";

export default function Dashboard(props) {
  const [getList, setList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [component, setcomponent] = useState(<Mainview />);
  const [getBatch, setBatch] = useState(false);

  useEffect(() => {
    chkToken();
    setList(StudeMenu);
  }, []);
  /// chk
  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      setLoading(false);
    } else {
      props.history.replace({ pathname: "/Dashboard" });
      setLoading(false);
    }
  };

  const handleLogOut = () => {
    localStorage.clear();
    props.history.replace({ pathname: "/Login" });
  };

  const handleCallPage = (inpt) => {
    // console.log("handleCallPage====inpt", inpt);
    switch (inpt) {
      case 1:
        setcomponent(<Mainview />);
        break;
      case 2:
        setcomponent(<Batches />);
        break;
      case 3:
        setcomponent(<Myhomeworks />);
        break;
      case 4:
        setcomponent(<Mygames />);
        break;
      case 5:
        setcomponent(<Tournaments />);

        break;
      case 6:
        setcomponent(<Myreviews />);

        break;
      case 7:
        setcomponent(<Referandearn />);
        break;
      case 8:
        setcomponent(<StudentProfile />);
        break;
      case 9:
        setcomponent(<MyCart handleCallPage={handleCallPage} />);
        break;
      case 10:
        {
          handleLogOut();
        }
        break;
      case 11:
        setcomponent(<PurchaseHistory />);

        break;
      case 12:
        setcomponent(<MyCourse handleCallPage={handleCallPage} />);
        break;

      case 13:
        setcomponent(<ClassDuration />);
        break;
      case 14:
        setcomponent(<CheckPayment />);
        break;
      case 15:
        setcomponent(<HelpAndSupport />);
        break;

      case 16:
        setcomponent(<Notifications handleCallPage={handleCallPage} />);
        break;

      case 17:
        setcomponent(<NotiDetails />);
        break;

      default:
        break;
    }
  };

  const handleTemp = (t) => {
    // console.log("handleTemp==in=Dashboard", t);
  };

  const MenuItemss = () => {
    // return getList.map((item) => {
    return (
      <div class="sidemenu-body menuitemsul heading">
        <ul
          class="sidemenu-nav metisMenu h-100"
          id="sidemenu-nav"
          data-simplebar
          // class="menuitemsul"
        >
          {adminRoutes.map((data) => (
            <li
              class="nav-item mm-active menuitemsli .sidemenu-area .sidemenu-body .sidemenu-nav .nav-item .nav-link::before "
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignSelf: "center",
              }}
            >
              <span class="collapsed-nav-link nav-link" aria-expanded="false">
                <span class="icon" style={{ marginRight: "20px" }}>
                  <i class={data.icon} style={{ color: "#18a0fb" }}></i>{" "}
                </span>
                {data.name === "Batches" ? (
                  <span
                    class="menu-title ac"
                    onClick={() =>
                      setcomponent(
                        <data.component handleCallPage={handleCallPage} />
                      )
                    }
                  >
                    {data.name}
                  </span>
                ) : (
                  <span
                    class="menu-title ac"
                    onClick={() => setcomponent(<data.component />)}
                  >
                    {data.name}
                  </span>
                )}
              </span>
            </li>
          ))}
        </ul>
      </div>
    );
    // });
  };

  const style = {
    padding: "300px 0",
    textAlign: "center",
    opacity: 1,
  };

  if (loading) {
    return (
      <div
      // style={{
      //   backgroundImage: `url(${valvo})`,
      //   backgroundSize: "cover",
      // }}
      >
        <div style={style}>
          <CircularProgress color="secondary" />
        </div>
      </div>
    );
  }

  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              class="w-100 container-fluid"
              style={{
                backgroundColor: "white",
                // width: "100%",
                display: "block",
              }}
            >
              <Header hideArrow="hideArrow" />
            </div>
          </div>
        </nav>
        <div
          style={{ backgroundColor: "#FFF", margin: "10px", marginTop: "0px" }}
        >
          <Mainview />
        </div>
        <Footer />
      </div>
    </>
  );
}
