import React, { useState, useEffect } from "react";
import { styled } from "@mui/material/styles";
import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import MuiAccordion from "@mui/material/Accordion";
import MuiAccordionSummary from "@mui/material/AccordionSummary";
import MuiAccordionDetails from "@mui/material/AccordionDetails";
import Button from "@mui/material/Button";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import Modal from "@material-ui/core/Modal";
import { postData, postDataAndImage } from "../config/FetchNodeServices";
import { checkRequire, checkMobile } from "../config/Checks";
import { useHistory, Redirect } from "react-router-dom";
import swal from "sweetalert";
import ListItems from "../ListItems";
import Header from "../dashboard/Header";
import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles = styled((theme) => ({
  input: {
    display: "none",
  },
  button: {
    margin: theme.spacing(1),
    width: 200,
  },
  center: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "row",
  },
  avatortheme: {
    width: "100px",
    height: "100px",
  },
}));

const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&:before": {
    display: "none",
  },
}));

const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={
      <ArrowForwardIosSharpIcon
        sx={{ fontSize: "0.9rem", flexDirection: "row" }}
      />
    }
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark"
      ? "rgba(255, 255, 255, .05)"
      : "rgba(0, 0, 0, .03)",
  flexDirection: "row-reverse",
  "& .MuiAccordionSummary-expandIconWrapper.Mui-expanded": {
    transform: "rotate(90deg)",
  },
  "& .MuiAccordionSummary-content": {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: "1px solid rgba(0, 0, 0, .125)",
}));

export default function StudentProfile(props) {
  // console.log("propssssss=======>>>>>", props);
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState("panel1");
  const [getList, setList] = useState([]);
  const [getOpen, setOpen] = useState(false);
  const [getOldPass, setOldPass] = useState("");
  const [getNewPass, setNewPass] = useState("");
  const [getConNewPass, setConNewPass] = useState("");
  const [getFirstName, setFirstName] = useState("");
  const [getLastName, setLastName] = useState("");
  const [getContactNumber, setContactNumber] = useState("");
  const [getPicture, setPicture] = useState({ pic: "", filePic: "" });
  const [getImage, setImage] = useState("");
  const [getFirstNameErr, setFirstNameErr] = useState("");
  const [getLastNameErr, setLastNameErr] = useState("");
  const [getContactErr, setContactErr] = useState("");
  const [getErrPic, setErrPic] = useState("");
  const [loading, setLoading] = useState(true);
  const [updatedName, setUpdatedName] = useState("");
  const history = useHistory();

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      // setLoading(false);
    } else {
      props.history.replace({ pathname: "/StudentProfile" });
      // setLoading(false);
    }
  };

  useEffect(() => {
    chkToken();
    showData();
  }, []);

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  const handlePicture = (event) => {
    setPicture({
      pic: event.target.files[0],
      filePic: URL.createObjectURL(event.target.files[0]),
    });
  };

  const handleSubmit = async () => {
    var User = JSON.parse(localStorage.getItem("User"));
    var email = User.email;
    var err = false;

    if (true) {
      var formData = new FormData();
      formData.append("email", email);
      formData.append("firstname", getFirstName);
      formData.append("lastname", getLastName);
      formData.append("Phone", getContactNumber);
      formData.append("Photo", getPicture.pic);
      var config = { headers: { "content-type": "multipart/form-data" } };
      var result = await postDataAndImage(
        "Student/UpdateStudent",
        formData,
        config
      );
      // console.log("RESULT OF UPDATE STUDENT------------------",result);
      if (result.status) {
        localStorage.removeItem("Account");
        swal("Profile Updated Successfully", " ", "success", {
          buttons: true,
        });
        showData();
        handleClose();
      } else {
        swal("Error Occurred", " ", "error", {
          buttons: true,
        });
      }
    } else {
      swal("Please enter missing filed to update profile", " ", "error", {
        buttons: true,
      });
    }
  };

  const ChangePassword = async () => {
    var User = JSON.parse(localStorage.getItem("User"));
    if (getOldPass === User.password) {
      if (getNewPass === getConNewPass) {
        let body = [
          {
            email: User.email,
            oldPass: getOldPass,
            newPass: getNewPass,
            conNewPass: getConNewPass,
          },
        ];
        let list = await postData("Student/ChangePassword", body);
        // console.log("LIST RESULT", list);
        if (list.status == true) {
          swal("Password Changed Successfully", " ", "success", {
            buttons: true,
          });
          localStorage.clear();
          history.replace({ pathname: "/Login" });
          // console.log("history", history);
          // props.history.replace({ pathname: "/Login" });
        } else {
          swal("Server Error...", " ", "error", {
            buttons: true,
          });
        }
      } else {
        swal("Password Not Matched", " ", "error", {
          buttons: true,
        });
      }
    } else {
      swal("All fields are required to change password", " ", "error", {
        buttons: true,
      });
    }
  };

  const myFunction = () => {
    setOpen(true);
    let fn = getList.Name.split(" ")[0];
    let ln = getList.Name.split(" ")[1];
    console.log("nnnnnnnnnnnnnnnnn================", fn + ln);

    setFirstName(fn);
    setLastName(ln);
    setContactNumber(getList.Phone);
    // setPicture(getList.PhotoURL__c);
    setImage(getList.PhotoURL__c);

    // setUpdatedName(getList.Name)
  };

  const handleClose = () => {
    setOpen(false);
    setFirstName("");
    setLastName("");
    setContactNumber("");
    setPicture({ pic: "", filepic: "" });
    setFirstNameErr("");
    setLastNameErr("");
    setContactErr("");
    setErrPic("");
    setImage("");
    showData();
  };

  const showData = async () => {
    var User = JSON.parse(localStorage.getItem("User"));

    let body = {
      email: User.email,
      id: User.id,
    };
    // console.log("Bodyyyyy==>>>>>>>>>>>>===", body);
    let list = await postData("Student/StudentDisplay", body);
    if (list.status) {
      // console.log("studentData======", list.result[0]);
      setList(list.result[0]);
      localStorage.setItem(
        "RefCode",
        JSON.stringify({
          RefCode: list.result[0].Referral_Code__c,
        })
      );
      // console.log("list.result[0].Name=============0", list.result[0].Name);
      localStorage.setItem(
        "Account",
        JSON.stringify({
          Name: list.result[0].Name,
        })
      );
    } else {
      // if (list.message === "You are not authorised") {
      swal("Record For This email Not Found", " ", "error", {
        buttons: true,
      });
    }
  };

  const EditProfile = () => {
    // return getList.map((item) => {
    return (
      <>
        <div style={{ flexDirection: "row" }}>
          <form class="needs-validation" novalidate="">
            <div class="container">
              <div class="row">
                <div class="col">
                  <label for="validationCustom01">First Name</label>
                  <img src={getFirstNameErr} width="10" height="10" />
                  <input
                    type="text"
                    className="form-control"
                    id="validationCustom01"
                    placeholder="Name"
                    value={getFirstName}
                    onChange={(event) => setFirstName(event.target.value)}
                  />
                </div>

                <div class="col">
                  <label for="validationCustom01">Last Name</label>
                  <img src={getLastNameErr} width="10" height="10" />
                  <input
                    type="text"
                    className="form-control"
                    id="validationCustom01"
                    placeholder="Name"
                    value={getLastName}
                    onChange={(event) => setLastName(event.target.value)}
                  />
                </div>

                <div class="col">
                  <label for="validationCustom01">Contact Number</label>
                  <img src={getContactErr} width="10" height="10" />
                  <input
                    type="text"
                    className="form-control"
                    id="validationCustom01"
                    placeholder="Contact Number"
                    value={getContactNumber}
                    onChange={(event) => setContactNumber(event.target.value)}
                  />
                </div>
                <hr />
              </div>
              <div class="row">
                <div class="col-md-6 p-5">
                  <span for="validationCustom01">
                    <b>Profile Picture</b>
                  </span>
                  <img src={getErrPic} width="10" height="10" />
                </div>
                <div class="col p-2">
                  <Avatar
                    sx={{
                      width: "70px",
                      height: "70px",
                      marginBottom: "5px",
                      marginLeft: "70px",
                    }}
                    alt="Remy Sharp"
                    src={getPicture}
                    className={classes.avatortheme}
                  />
                  <input
                    style={{ paddingLeft: "20px" }}
                    hidden
                    accept="image/*"
                    className={classes.input}
                    id="contained-button-filepic"
                    multiple
                    type="file"
                    onChange={(event) => handlePicture(event)}
                  />
                  <label htmlFor="contained-button-filepic">
                    <Button
                      style={{
                        marginLeft: "20px",
                        marginTop: "10px",
                        marginBottom: "5px",
                        backgroundColor: "#18a0fb",
                      }}
                      variant="contained"
                      color="primary"
                      className={classes.button}
                      startIcon={<CloudUploadIcon />}
                      component="span"
                      onChange={(event) => handlePicture(event)}
                    >
                      Upload Picture
                    </Button>
                  </label>
                </div>
              </div>
            </div>
          </form>
        </div>
      </>
    );
    // });
  };
  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              // class="welcome-area"
              style={{
                backgroundColor: "white",
                width: "100%",
              }}
            >
              <Header history={props.history} />
            </div>
          </div>
        </nav>
        <div>
          <div class="card">
            <Box
              component="span"
              sx={{ backgroundColor: "#ecf0f1", padding: "2%" }}
            >
              <b class="heading">PERSONAL DETAILS</b>
            </Box>

            {/* <div class="container"> */}
            <div class="row">
              <div class="col-md-2 pt-5 ">
                {/* <Stack direction="row"> */}
                <Avatar
                  alt="Cindy Baker"
                  // src="images/avatar/3.png"
                  src={getList.PhotoURL__c}
                  sx={{ width: 100, height: 100, marginLeft: "15%" }}
                />
                {/* </Stack> */}
                <div
                  style={{
                    padding: "0.5%",
                    fontSize: "12px",
                    marginTop: "10%",
                  }}
                >
                  <div class="stupro">STUDENT ID: {getList.Student_Id__c}</div>
                </div>
                <div
                  style={{
                    fontSize: "12px",
                    padding: "0.5%",
                    marginTop: "10%",
                  }}
                >
                  <div class="stupro">FIDE ID: {getList.FIDE_Id__c} </div>
                </div>
              </div>

              <div class="col-md-10 ">
                <table class="table table-borderless m-4 heading">
                  <tr>
                    <th>NAME</th>
                    <td> {getList.Name} </td>
                  </tr>
                  <tr>
                    <th> DATE OF BIRTH </th>
                    <td> {getList.Date_of_birth__c}</td>
                  </tr>
                  <tr>
                    <th>PARENT'S DETAILS</th>
                    <td>
                      <label style={{ display: "inherit", color: "gray" }}>
                        <b> Name </b>
                      </label>
                      {getList.Guardian_s_Name__c}
                    </td>
                    <td>
                      <label style={{ display: "inherit", color: "gray" }}>
                        <b>Contact no.</b>
                      </label>
                      {getList.Guardian_s_Mobile_No__c}
                    </td>
                    <td>
                      <label style={{ display: "grid" }}>
                        <b style={{ color: "grey" }}>Email</b>

                        {getList.Guardian_s_Email__c}
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <th> LEVEL </th>
                    <td>{getList.Level__c}</td>
                  </tr>
                  <tr>
                    <th> COUNTRY </th>
                    <td>{getList.Country__c}</td>
                  </tr>
                  <tr>
                    <th> UA RATING </th>
                    <th>
                      Classical |{" "}
                      <span style={{ color: "grey" }}>
                        {getList.UA_rating__c}{" "}
                      </span>
                    </th>
                    <th>
                      Blitz |
                      <span style={{ color: "grey" }}>
                        &nbsp; {getList.UA_Rating_Blitz__c}
                      </span>
                    </th>

                    <th>
                      Rapid |
                      <span style={{ color: "grey" }}>
                        &nbsp; {getList.UA_Rating_Rapid__c}
                      </span>
                    </th>
                  </tr>
                  <tr>
                    <th> FIDE RATING </th>
                    <th>
                      Classical |{" "}
                      <span style={{ color: "grey" }}>
                        {getList.FIDE_Rating__pc}{" "}
                      </span>
                    </th>
                    <th>
                      Blitz |
                      <span style={{ color: "grey" }}>
                        &nbsp; {getList.FIDE_Rating_Blitz__c}
                      </span>
                    </th>
                    <th>
                      Rapid |
                      <span style={{ color: "grey" }}>
                        &nbsp; {getList.FIDE_Rating_Rapid__c}
                      </span>
                    </th>
                  </tr>
                </table>
              </div>
              {/* <div class="col-md-2"> */}
              {/* <table class="table table-borderless m-4 heading"> */}
              {/* <i
                      class="bi bi-pencil-square "
                      onClick={myFunction}
                      style={{ marginLeft: "65%" }}
                    ></i> */}
              {/* <Modal
                      style={{
                        marginTop: "10%",
                        marginLeft: "30%",
                        marginRight: "20%",
                        border: 0,
                      }}
                      open={getOpen}
                      onClose={handleClose}
                      aria-labelledby="modal-modal-title"
                      aria-describedby="modal-modal-description"
                    >
                      <Box
                        sx={{
                          backgroundColor: "white",
                          width: "100%",
                          border: 0,
                        }}
                      >
                        <div class="modal-header">
                          <div class="heading">
                            <b>Update Student Profile</b>
                          </div>

                          <i
                            style={{
                              cursor: "pointer",
                            }}
                            class="bi bi-x-lg"
                            aria-hidden="true"
                            onClick={handleClose}
                          ></i>
                        </div>
                        {EditProfile()}

                        <div class="modal-footer">
                          <div class="container">
                            <div class="row">
                              <div class="col">
                                <button
                                  type="button"
                                  class="btn btn-primary"
                                  style={{ backgroundColor: "#18a0fb" }}
                                  onClick={handleClose}
                                >
                                  Cancel
                                </button>
                              </div>
                              <div class="col">
                                <button
                                  type="button"
                                  class="btn btn-primary"
                                  style={{ backgroundColor: "#18a0fb" }}
                                  onClick={handleSubmit}
                                >
                                  Submit
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </Box>
                    </Modal> */}
              {/* </table> */}
              {/* </div> */}
            </div>
            {/* </div> */}

            <Accordion
              expanded={expanded === "panel1"}
              onChange={handleChange("panel1")}
              style={{ padding: "2%" }}
            >
              <AccordionSummary
                aria-controls="panel1d-content"
                id="panel1d-header"
              >
                <div class="heading">
                  <b>CHANGE PASSWORD</b>
                </div>
              </AccordionSummary>
              <AccordionDetails>
                <div class="heading" style={{ padding: "2%", display: "flex" }}>
                  <b>OLD PASSWORD</b>

                  <input
                    type="password"
                    class="form-control bordClr"
                    placeholder=""
                    value={getOldPass}
                    style={{ width: "40%", marginLeft: "18%" }}
                    onChange={(event) => setOldPass(event.target.value)}
                  />
                </div>
                <div class="heading" style={{ padding: "2%", display: "flex" }}>
                  <b>NEW PASSWORD</b>
                  <input
                    type="password"
                    class="form-control bordClr"
                    placeholder=""
                    value={getNewPass}
                    style={{ width: "40%", marginLeft: "17.7%" }}
                    onChange={(event) => setNewPass(event.target.value)}
                  />
                </div>
                <div class="heading" style={{ padding: "2%", display: "flex" }}>
                  <b>CONFIRM</b> &nbsp; <b>PASSWORD</b>
                  <input
                    type="password"
                    class="form-control bordClr"
                    style={{ borderColor: "#18a0fb" }}
                    placeholder=""
                    value={getConNewPass}
                    style={{ width: "40%", marginLeft: "13.7%" }}
                    onChange={(event) => setConNewPass(event.target.value)}
                  />
                </div>
                <button
                  type="submit"
                  class="buttonB"
                  onClick={() => ChangePassword()}
                  style={{ marginTop: "2%", marginLeft: "1.5%", width: "15%" }}
                >
                  Save
                </button>
              </AccordionDetails>
            </Accordion>
          </div>
        </div>
      </div>
    </>
  );
}
