//To send data in node
const postData = async (url, body) => {
  var tokenIntial = localStorage.getItem("token");
  var tokenFinal = JSON.parse(tokenIntial);

  try {
    const response = await fetch(`${ServerURL}/${url}`, {
      method: "post",
      mode: "cors",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
        Authorization: `Bearer ${tokenFinal.token}`,
      },
      body: JSON.stringify(body),
    });
    const result = await response.json();
    // console.log("POST DATA SERVICE CONSOLE", result);
    if (result == "Session has Expired Please Login Again") {
      alert("Session has Expired Please Login Again");
      return false;
    } else {
      // console.log(result);
      return result;
    }
  } catch (e) {
    return null;
  }
};

