import React, { useEffect, useState } from "react";
import {
  getData,
  postData,
  postDataWithoutToken,
  postDataAndImage,
  ServerURL
} from "../config/FetchNodeServices";
import ListItems from "../ListItems";
import Header from "./Header";
import Button from "@material-ui/core/Button";
import { styled } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import Draggable from "react-draggable";
import { PDFViewer } from "@react-pdf/renderer";
import { PDFReader } from "reactjs-pdf-reader";
import swal from "sweetalert";

import { SignalWifiStatusbarNullSharp } from "@mui/icons-material";

function PaperComponent(props) {
  return (
    <Draggable
      handle="#draggable-dialog-title"
      cancel={'[class*="MuiDialogContent-root"]'}
    >
      <Paper {...props} />
    </Draggable>
  );
}
var user = "";
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};
export default function Myhomeworks(props) {
  const [getCurrentList, setCurrentList] = useState([]);
  const [getPastList, setPastList] = useState([]);
  const [getIcon, setIcon] = useState({ icon: "", fileicon: "" });
  const [Con_for_PastLevels, setConForPastLevels] = useState(true);
  const [session_Id, setSession_Id] = useState("");
  const [EnrollId, setEnrollId] = useState("");
  const [NoCurrentHomewok, setNoCurrentHomewok] = useState("");
  const [checkCon, setCheckCon] = useState(false);

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      // setLoading(false);
    } else {
      props.history.replace({ pathname: "/My Homework" });
      // setLoading(false);
    }
  };

  const handleIcon = (event) => {
    setIcon({
      icon: event.target.files[0],
      fileicon: URL.createObjectURL(event.target.files[0]),
    });
  };

  // Submit to Backend ....
  const submitFunc = async () => {
    handleClose();
    let formData = new FormData();
    formData.append("homework_doc", getIcon.icon);
    formData.append("session_id", session_Id);
    formData.append("is_completed", true);
    formData.append("EnrollId", EnrollId);
    formData.append("studentAcc_id", user.id);
    // console.log("formData=====", formData);
    var config = { headers: { "content-type": "multipart/form-data" } };
    let resultResponse = await postDataAndImage(
      "Homework/uploadHomework",
      formData,
      config
    );
    // console.log("resultResponse===========", resultResponse);
    if (resultResponse.status) {
      swal("Your homework is uploaded", " ", "success", {
        buttons: true,
      });
      handleClose();
      fetchDataForCurrentHomeworks();
      fetchDataForPastHomeworks();
    } else {
      swal("Error Occurred", " ", "error", {
        buttons: true,
      });
    }
  };

  //

  // Dioluge
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = (idd) => {
    // console.log("iddddSession===", idd);
    setSession_Id(idd);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  ///

  //DateTime function
  const DisplayDate = (Coming_date) => {
    var newDate = new Date(Coming_date);
    // console.log("newDate===", newDate);
    var dd = newDate.getDate();
    // console.log("dd=====", dd);
    var mm = newDate.toLocaleString("default", { month: "long" });
    // console.log("mm=====", mm);
    var yy = newDate.getFullYear();
    // console.log("yy=======", yy);

    var nameday = new Date(newDate).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);

    var finalDate = `${dd}  ${mm} ${yy}`;
    // console.log("finalDate", finalDate);
    return finalDate;
  };

  const DisplayDay = (Coming_date) => {
    var newDay = new Date(Coming_date);

    var nameday = new Date(newDay).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);
    return nameday;
  };
  ////  // "start": "react-scripts start",
  // "build": "react-scripts build",

  // "start": "react-scripts --max_old_space_size=4096 start",
  //  "build": "react-scripts --max_old_space_size=4096 build",

  const fetchDataForCurrentHomeworks = async () => {
    user = JSON.parse(localStorage.getItem("User"));
    // var e_id = user.email;
    var e_id = user.id;
    // console.log("e_iddddddddddd++++===", e_id);
    let homeworkResult = await postData("Homework/showHomeworks", {
      // email: e_id,
      id: e_id,
    });
    // console.log(
    //   "homeworkResult.resultt[0].enroll_id",
    //   homeworkResult.resultt[0].enroll_id
    // );
    if (homeworkResult.resultt.length != 0) {
      setEnrollId(homeworkResult.resultt[0].enroll_id);
      setCurrentList(homeworkResult.resultt);
      // console.log("datatatatta=====", homeworkResult.resultt);
    } else {
      setCheckCon(true);
      setNoCurrentHomewok("No current homeworks available in record");
    }
  };

  const fetchDataForPastHomeworks = async () => {
    var user = JSON.parse(localStorage.getItem("User"));
    // var e_id = user.email;
    var e_id = user.id;
    // console.log("e_iddddddddddd++++===", e_id);
    let homeworkResult = await postData(
      "Homework/showPastHomeworks",
      // {email: e_id,}
      { id: e_id }
    );
    if (homeworkResult.resultt.length < 1) {
      setConForPastLevels(false);
    }
    setPastList(homeworkResult.resultt);
    // console.log("datatatatta=====", homeworkResult.resultt);
  };

  useEffect(() => {
    chkToken();
    fetchDataForCurrentHomeworks();
    fetchDataForPastHomeworks();
  }, []);

  const Input = styled("input")({
    display: "none",
  });

  const ShowDataForCurrentHomeworks = () => {
    return getCurrentList.map((item, index) => {
      // console.log("itemmmmm=====ShowDataForCurrentHomeworks=", item);
      return (
        <>
          <tbody>
            <tr>
              <td scope="row">{index < 10 ? `0${index + 1}` : index + 1}</td>
              {/* <td>{item.Topic__c}</td> */}
              <td>
                <div
                  style={{
                    width: "280px",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                  }}
                >
                  {" "}
                  {item.Topic__c}
                </div>
              </td>
              <td>
                {/* <label style={{ display: "inherit", color: "grey" }}>
                  {DisplayDay(item.Date__c)}
                </label>
                {DisplayDate(item.Date__c)} */}
                <p style={{ color: "grey", fontSize: 11 }}>
                  {DisplayDay(item.Date__c)}
                </p>
                <p
                  style={{ color: "#2e3b52", fontSize: 13, fontWeight: "bold" }}
                >
                  {DisplayDate(item.Date__c)}
                </p>
              </td>
              <td>
                {/* <label style={{ display: "inherit", color: "grey" }}>
                  {DisplayDay(item.Homework_Deadline__c)}
                </label>
                {DisplayDate(item.Homework_Deadline__c)} */}
                <p style={{ color: "grey", fontSize: 11 }}>
                  {DisplayDay(item.Homework_Deadline__c)}
                </p>

                <p
                  style={{ color: "#2e3b52", fontSize: 13, fontWeight: "bold" }}
                >
                  {DisplayDate(item.Homework_Deadline__c)}
                </p>
              </td>
              <td>
                {/* <div style={{ width: "150px" }}> */}
                {/* <a
                  href={`/homework/${item.Homework_PDF_Code_Name__c}`}
                  download
                  target={"_blank"}
                  style={{ textDecoration: "none", color: "white" }}
                > */}
                <button
                  class="btn btn-primary rounded-pill"
                  onClick={handleClickOpen}
                >
                  View Assignment
                </button>
              </td>

              <td>
                <a
                  href={`${ServerURL}/homework_files/${item.Homework_PDF_Code_Name__c}.pdf`}
                  download
                  target={"_blank"}
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <button
                    class="btn btn-primary rounded-pill"
                    // onClick={handleClickOpen}
                  >
                    {" "}
                    Download Assignment
                  </button>
                </a>
              </td>
            </tr>
          </tbody>
        </>
      );
    });
  };

  const showPdfRead = () => {
    return getCurrentList.map((item, index) => {
      return (
        <>
          <PDFReader style={{overflow:"hidden"}} url={`${ServerURL}/homework_files/${item.Homework_PDF_Code_Name__c}.pdf`} />
        </>
      );
    });
  };

  ///Past Homework
  const ShowDataForPasttHomeworks = () => {
    return getPastList.map((item, index) => {
      return (
        <>
          <tbody>
            <tr>
              <td scope="row">{index < 10 ? `0${index + 1}` : index + 1}</td>
              {/* <td>{item.Topic__c}</td> */}
              <td>
                <div
                  style={{
                    width: "280px",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                  }}
                >
                  {" "}
                  {item.Topic__c}
                </div>
              </td>
              <td>
                {/* <label style={{ display: "inherit", color: "grey" }}>
                  {DisplayDay(item.Date__c)}
                </label>
                {DisplayDate(item.Date__c)} */}
                <p style={{ color: "grey", fontSize: 11 }}>
                  {DisplayDay(item.Date__c)}
                </p>
                <p
                  style={{ color: "#2e3b52", fontSize: 13, fontWeight: "bold" }}
                >
                  {DisplayDate(item.Date__c)}
                </p>
              </td>
              <td>
                {/* <label style={{ display: "inherit", color: "grey" }}>
                  {DisplayDay(item.Homework_Deadline__c)}
                </label>     
                {DisplayDate(item.Homework_Deadline__c)} */}
                <p style={{ color: "grey", fontSize: 11 }}>
                  {DisplayDay(item.Homework_Deadline__c)}
                </p>
                <p
                  style={{ color: "#2e3b52", fontSize: 13, fontWeight: "bold" }}
                >
                  {DisplayDate(item.Homework_Deadline__c)}
                </p>
              </td>
              <td>
                {/* <a
                  href={`/homework/${item.Homework_PDF_Code_Name__c}`}
                  download
                  target={"_blank"}
                  style={{ textDecoration: "none", color: "white" }}
                > */}
                <button class="btn btn-primary rounded-pill" onClick={handleClickOpen}>
                  View Assignment
                </button>
                {/* <button
                    class="buttonHW btn "
                    style={{
                      width: "90%",
                      marginLeft: "30px",
                    }}
                  >
                    {" "}
                    View Assignment
                  </button> */}
                {/* </a> */}
              </td>

              <td>
                <a
                  href={`${ServerURL}/homework_files/${item.Homework_PDF_Code_Name__c}.pdf`}
                  download
                  target={"_blank"}
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <button class="btn btn-primary rounded-pill">
                    Download homework
                  </button>
                  {/* <button
                    class="buttonHW"
                    style={{
                      width: "100%",
                      marginLeft: "10px",
                      paddingLeft: "0px",
                      paddingRight: "0px",
                    }}
                  >
                    Download homework
                  </button> */}
                </a>
              </td>
            </tr>
          </tbody>
        </>
      );
    });
  };

  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              // class="welcome-area"
              style={{
                backgroundColor: "white",
                width: "100%",
              }}
            >
              <Header history={props.history} />
            </div>
          </div>
        </nav>
        <div class="mt-3 heading" style={{ padding: 25 }}>
          <div>
            <b style={{ margin: 30 }}>CURRENT HOMEWORK</b>
          </div>
          <div class="card mt-4">
            <table
              class="table table-borderless m-2"
              style={{
                borderCollapse: "collapse",
                borderRadius: "1em",
                overflow: "hidden",
              }}
            >
              <thead class="" style={{ backgroundColor: "#f9f9f9" }}>
                <tr
                  style={{
                    fontSize: "13px",
                    justifyContent: "center",
                    color: "#738097",
                  }}
                >
                  <th
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  >
                    S NO.
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  >
                    TOPIC
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  >
                    ASSIGNED ON
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  >
                    DEADLINE
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  ></th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700", padding: 16 }}
                  ></th>
                </tr>
              </thead>
              {!checkCon ? (
                ShowDataForCurrentHomeworks()
              ) : (
                <p
                  style={{
                    position: "relative",
                    left: "50%",
                    marginTop: "10%",
                  }}
                >
                  {NoCurrentHomewok}
                </p>
              )}
            </table>
          </div>
          <Dialog
            // fullScreen
            open={open}
            onClose={handleClose}
            PaperComponent={PaperComponent}
            aria-labelledby="draggable-dialog-title"
          >
            <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
              View Assignment
            </DialogTitle>
            <DialogContent>
              {/* <div
                style={{
                  display: "flex",
                  justifyContent: "space-around",
                  // marginTop: "5%",
                }}
              > */}
                {/* <PDFReader url="/images/Ashutosh_Johri.pdf" /> */}
                {/* <PDFReader url={`/homework/${item.Homework_PDF_Code_Name__c}`} /> */}
                {showPdfRead()}
              {/* </div> */}
            </DialogContent>
            <DialogActions>
              <Button
                autoFocus
                onClick={handleClose}
                style={{
                  backgroundColor: "#18a0fb",
                  display: "flex",
                  width: "400px",
                  right: "100px",
                }}
              >
                CLose
              </Button>
              <a
                // href={"/images/Ashutosh_Johri.pdf"}
                // href={`/homework/${item.Homework_PDF_Code_Name__c}`}
                // href={`${ServerURL}/homework_files/${item.Homework_PDF_Code_Name__c}.pdf`}
                download
                target={"_blank"}
                style={{ textDecoration: "none", color: "white" }}
              >
                {/* <Button
                  autoFocus
                  // onClick={handleClose}
                  // onClick={submitFunc}
                  style={{ backgroundColor: "#18a0fb", width: "50%px" }}
                >
                  Download
                </Button> */}
              </a>
            </DialogActions>
          </Dialog>
        </div>
        {/* */}

        {Con_for_PastLevels ? (
          <div class="mt-3 heading" style={{ padding: 25 }}>
            <div>
              <b style={{ margin: 30 }}>PAST HOMEWORK</b>
            </div>
            <div class="card mt-4">
              <table
                class="table table-borderless m-2"
                style={{
                  borderCollapse: "collapse",
                  borderRadius: "1em",
                  overflow: "hidden",
                }}
              >
                <thead class="" style={{ backgroundColor: "#f9f9f9" }}>
                  <tr
                    style={{
                      fontSize: "13px",
                      justifyContent: "center",
                      color: "#738097",
                    }}
                  >
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    >
                      S NO.
                    </th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    >
                      TOPIC
                    </th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    >
                      ASSIGNED ON
                    </th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    >
                      DEADLINE
                    </th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    ></th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        padding: 16,
                      }}
                    ></th>
                  </tr>
                </thead>

                {ShowDataForPasttHomeworks()}
              </table>
            </div>
          </div>
        ) : null}
      </div>
    </>
  );
}
