import React, { useState, useEffect } from "react";
import ListItems from "../ListItems";
import Header from "../dashboard/Header";
import { postData } from "../config/FetchNodeServices";

export default function PurchaseHistory(props) {
  const [getList, setList] = useState([]);

  const fetchData = async () => {
    var user = JSON.parse(localStorage.getItem("User"));
    let PaymentResult = await postData("Payment/showPayments", { id: user.id });
    console.log("PaymentResult=====", PaymentResult.result);
    setList(PaymentResult.result);
  };

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      // setLoading(false);
    } else {
      props.history.replace({ pathname: "/Purchase History" });
      // setLoading(false);
    }
  };

  useEffect(() => {
    chkToken();
    fetchData();
  }, []);

  const showPayments = () => {
    return getList.map((item, index) => {
      return (
        <>
          <tbody>
            <tr>
              <td scope="row">{index + 1}</td>
              <td>{item.Class_Type__c}</td>
              <td>{item.Level__c}</td>
              <td>21 May 2021</td>
              <td>{item.Payment_Date__c}</td>
              <td>{item.Payment_Amount__c}</td>
              <td style={{ color: "#2ecc71" }}>Paid</td>
              <td>
                <button class="buttonPH">Invoice</button>
              </td>{" "}
            </tr>
          </tbody>
        </>
      );
    });
  };

  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              // class="welcome-area"
              style={{
                backgroundColor: "white",
                width: "100%",
              }}
            >
              <Header history={props.history} />
            </div>
          </div>
        </nav>
        <div class="mt-5 heading">
          <div style={{ margin: "2%", marginLeft: "1%" }}>
            <b>MY ORDERS</b>
          </div>
          <table class="table table-borderless m-2">
            <thead class="thead-light">
              <tr>
                <th scope="col">SR. No</th>
                <th scope="col">CLASS NAME</th>
                <th scope="col">LEVEL</th>
                <th scope="col">DUE DATE</th>
                <th scope="col">PAYMENT DATE</th>
                <th scope="col">FEE AMOUNT</th>
                <th scope="col">STATUS</th>
                <th scope="col"></th>
              </tr>
            </thead>
            {/* <tbody>
              <tr>
                <td scope="row">01</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#2ecc71" }}>Paid</td>
                <td>
                  <button class="buttonPH">Invoice</button>
                </td>{" "}
              </tr>
              <tr>
                <td scope="row">02</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#2ecc71" }}>Paid</td>
                <td>
                  <button class="buttonPH">Invoice</button>
                </td>{" "}
              </tr>
              <tr>
                <td scope="row">03</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#2ecc71" }}>Paid</td>
                <td>
                  <button class="buttonPH">Invoice</button>
                </td>{" "}
              </tr>
              <tr>
                <td scope="row">04</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#2ecc71" }}>Paid</td>
                <td>
                  <button class="buttonPH">Invoice</button>
                </td>{" "}
              </tr>
              <tr>
                <td scope="row">05</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#2ecc71" }}>Paid</td>
                <td>
                  <button class="buttonPH">Invoice</button>
                </td>{" "}
              </tr>
              <tr>
                <td scope="row">06</td>
                <td>One on One</td>
                <td>Beginner</td>
                <td>21 May 2021</td>
                <td>21 May 2021</td>
                <td>1000</td>
                <td style={{ color: "#e74c3c" }}>Paid</td>
                <td>
                  <button class="buttonPHG">Pay Now</button>
                </td>{" "}
              </tr>
            </tbody> */}
            {showPayments()}
          </table>
        </div>
      </div>
    </>
  );
}
