import React, { useEffect, useState } from "react";
import { postData } from "../config/FetchNodeServices";

export default function CurrentBatch() {
  const [batchId, setBatchId] = useState("");
  const [JoiningDate, setJoiningDate] = useState("");
  const [getClass, setClass] = useState("");
  const [RelManagerName, setRelManagerName] = useState("");
  const [getContact, setContact] = useState("");
  const [getEmail, setEmail] = useState("");
  const [coachName, setCoachName] = useState("");
  const [getLevel, setLevel] = useState("");
  const [NoCurrent, setNoCurrent] = useState("");
  const [checkCon, setCheckCon] = useState(false);

  useEffect(() => {
    try {
      fetchData();
    } catch (e) {
      // console.log("e", e);
    }
  }, []);

  const fetchData = async () => {
    var user = JSON.parse(localStorage.getItem("User"));
    // var e_id = user.email;
    var e_id = user.id;
    // console.log("e_idddddddddcurrentBatchdd++++===", e_id);
    let DataResult = await postData(
      "BatchApi/CurrentBatchDisplay",
      // { email: e_id }
      { id: e_id }
    );
    // console.log("DataResult=========", DataResult.result);
    if (DataResult.result.length != 0) {
      setBatchId(DataResult.result[0].Batch_id__c);
      setJoiningDate(DataResult.result[0].Enrollment_Date__c);
      setClass(DataResult.result[0].Class_Type__c);
      setRelManagerName(DataResult.result[0].Relationship_Manager__c);
      setContact(DataResult.result[0].Phone);
      setEmail(DataResult.result[0].Email__c);
      setCoachName(DataResult.result[0].Coach__c);
      setLevel(DataResult.result[0].Level__c);
      localStorage.setItem(
        "S_Level",
        JSON.stringify({
          level: DataResult.result[0].Level__c,
          // name: LogInResult.final_Resultt.records[0].Name,
          // email: LogInResult.final_Resultt.records[0].Email__c,
          // id: LogInResult.final_Resultt.records[0].Id,
          // password: LogInResult.final_Resultt.records[0].Student_Passowrd__c,
          // country: LogInResult.acountresult[0].Country__c,
          // city: LogInResult.acountresult[0].city,
          // state : LogInResult.acountresult[0].state
        })
      );
    } else {
      setCheckCon(true);
      setNoCurrent("No Current Batch in Record");
    }
  };

  const DisplayDate = (Coming_date) => {
    var newDate = new Date(Coming_date);
    // console.log("newDate===", newDate);
    var dd = newDate.getDate();
    // console.log("dd=====", dd);
    var mm = newDate.toLocaleString("default", { month: "long" });
    // console.log("mm=====", mm);
    var yy = newDate.getFullYear();
    // console.log("yy=======", yy);

    var nameday = new Date(newDate).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);

    var finalDate = `${dd}  ${mm} ${yy}`;
    // console.log("finalDate", finalDate);
    return finalDate;
  };

  return (
    <>
      <div
        class="card mt-4 heading"
        style={{
          backgroundColor: "#ecf0f1",
          borderRadius: "5px",
          marginTop: "1.7%",
        }}
      >
        <div>
          <b style={{ fontWeight: "bold" }}>CURRENT BATCH</b>
        </div>
      </div>
      {}

      {!checkCon ? (
        <>
          <div class="card heading">
            <div class="row">
              <div
                class="col-md-3"
                style={{ fontWeight: "700", fontSize: "13px" }}
              >
                BATCH ID
              </div>
              <div class="col-md-3 ">{batchId}</div>
              <div class="col-md-3 ">
                <label style={{ display: "table-cell", color: "gray" }}>
                  <b style={{ fontWeight: "normal" }}>Joining date</b>
                </label>
                <span>{DisplayDate(JoiningDate)} </span>
              </div>
              <div class="col-md-3 ">
                <label style={{ display: "table-cell", color: "gray" }}>
                  <b>Class</b>
                </label>{" "}
                {getClass}
              </div>
            </div>
          </div>
          <div class="card heading">
            <div class="row">
              <div
                class="col-md-3"
                style={{ fontWeight: "700", fontSize: "13px" }}
              >
                RELATIONSHIP MANAGER{" "}
              </div>
              <div class="col-md-3">
                {/* <label style={{ display: "table-cell", color: "gray" }}>
              <b>Name</b>
            </label> */}
                {RelManagerName}
              </div>
              <div class="col-md-3">
                <label style={{ display: "table-cell", color: "gray" }}>
                  <b>Contact No.</b>
                </label>
                {getContact}
              </div>
              <div class="col-md-3">
                <label style={{ display: "table-cell", color: "gray" }}>
                  <b>Email</b>
                </label>
                <span> {getEmail} </span>
              </div>
            </div>
          </div>
          <div class="card heading">
            <div class="row">
              <div
                class="col-md-3"
                style={{ fontWeight: "700", fontSize: "13px" }}
              >
                COACH
              </div>
              <div class="col-md-3">
                {/* <label style={{ display: "table-cell", color: "gray" }}>
              <b>Name</b>
            </label> */}
                {coachName}
              </div>
            </div>
          </div>
          <div class="card heading">
            <div class="row">
              <div
                class="col-md-3"
                style={{ fontWeight: "700", fontSize: "13px" }}
              >
                LEVEL
              </div>
              <div class="col-md-3">{getLevel}</div>
            </div>
          </div>
        </>
      ) : (
        <p style={{ position: "relative", left: "40%", marginTop: "10%" }}>
          {NoCurrent}
        </p>
      )}
    </>
  );
}
