import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { postData } from "../config/FetchNodeServices";
import ListItems from "../ListItems";
import ClassDuration from "./ClassDuration";
import Dashboard from "./Dashboard";
import Header from "./Header";
import Mygames from "./Mygames";
import Popover from "@mui/material/Popover";
import Typography from "@mui/material/Typography";
// import PopoverBid from ""

export default function Batches(props) {
  const [getList, setList] = useState([]);
  const [getLevel, setLevel] = useState("");
  const [getClass_Type, setClass_Type] = useState("");
  const [getBatchId, setBatchId] = useState("");
  const [getStudentId, setStudentId] = useState("");
  const [getClassDuration, setClassDuration] = useState("");
  const [NoBatch, setNoBatch] = useState("");
  const [checkCon, setCheckCon] = useState(false);
  const [loading, setloading] = useState(true);
  const [tomorrowDate, setTomorrowDate] = useState(false);
  const [tomorrowDateData, setTomorrowDateData] = useState({});
  ///popover

  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorEl2, setAnchorEl2] = React.useState(null);
  const [anchorEl3, setAnchorEl3] = React.useState(null);

  const handlePopoverOpen3 = (event) => {
    setAnchorEl3(event.currentTarget);
  };

  const handlePopoverClose3 = () => {
    setAnchorEl3(null);
  };

  const open3 = Boolean(anchorEl3);

  const handlePopoverOpen2 = (event) => {
    setAnchorEl2(event.currentTarget);
  };

  const open2 = Boolean(anchorEl2);

  const handlePopoverClose2 = () => {
    setAnchorEl2(null);
  };

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  ///popover

  //DateTime function
  const DisplayDate = (Coming_date) => {
    var newDate = new Date(Coming_date);
    // console.log("newDate===", newDate);
    var dd = newDate.getDate();
    // console.log("dd=====", dd);
    var mm = newDate.toLocaleString("default", { month: "long" });
    // console.log("mm=====", mm);
    var yy = newDate.getFullYear();
    // console.log("yy=======", yy);

    var nameday = new Date(newDate).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);

    var finalDate = `${dd}  ${mm} ${yy}`;
    // console.log("finalDate", finalDate);
    return finalDate;
  };

  const DisplayDay = (Coming_date) => {
    var newDay = new Date(Coming_date);

    var nameday = new Date(newDay).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);
    return nameday;
  };
  ////

  const DisplayLocalTime = (time) => {
    // console.log("LOCALTIMEEEEEE[0]", time.split(":")[0]);
    // console.log("LOCALTIMEEEEEE[1]", time.split(":")[1]);
    var hh = time.split(":")[0];
    var mm = time.split(":")[1];

    if (hh >= 12) {
      var tt = `${hh}:${mm} PM`;
    } else {
      var tt = `${hh}:${mm} AM`;
    }
    return tt;
  };

  const history = useHistory();
  const handleGo = (inpt) => {
    props.handleCallPage(inpt);
  };

  useEffect(() => {
    chkToken();
    try {
      fetchData();
      fetchDataForUpcoming();
    } catch (ee) {
      // console.log("ee", ee);
    }
  }, []);

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      // setLoading(false);
    } else {
      props.history.replace({ pathname: "/Batches" });
      // setLoading(false);
    }
  };

  const fetchData = async () => {
    try {
      var user = JSON.parse(localStorage.getItem("User"));
      var e_id = user.email;
      // var e_id = user.id;
      // console.log("e_idddddddddcurrentBatchdd++++===", e_id);
    } catch (e) {
      // console.log("e", e);
    }
    let BatchViewResult = await postData(
      "BatchApi/BatchViewDisplay",

      { email: e_id, idd: user.id }
    );
    setloading(false);
    // console.log("BatchViewResult", BatchViewResult.result);
    if (BatchViewResult.result.length != 0) {
      setList(BatchViewResult.result);
      setLevel(BatchViewResult.result[0].Level__c);
      setClass_Type(BatchViewResult.result[0].Class_Type__c);
      setBatchId(BatchViewResult.result[0].Batch_id__c);
      setStudentId(BatchViewResult.result[0].Student_id);
      setClassDuration(BatchViewResult.result[1].Class_Duration__c);
    } else {
      setCheckCon(true);
      setNoBatch("No Sessions");
    }
  };

  const fetchDataForUpcoming = async () => {
    try {
      var user = JSON.parse(localStorage.getItem("User"));
      var e_id = user.email;
      // var e_id = user.id;
      // console.log("e_idddddddddcurrentBatchdd++++===", e_id);
    } catch (e) {
      // console.log("e", e);
    }
    let BatchViewResultUpcoming = await postData(
      "BatchApi/BatchViewDisplayForPlanned",

      { email: e_id, idd: user.id }
    );
    // setloading(false);
    console.log(
      "BatchViewResultUpcoming====181",
      BatchViewResultUpcoming.result
    );
    setTomorrowDateData(BatchViewResultUpcoming.result);
  };

  const GoToZoomPage = (item, index) => {
    console.log("item in session==========187", item);
    props.history.push(
      { pathname: "/ZoomView" },
      {
        SessionName: item.Topic__c,
        CourseName: item.session_level,
        Duration: item.Class_Duration__c,
        Type: item.Class_Type__c,
      }
    );
  };

  const fillData = () => {
    // const LocalT = new Date();
    return getList.map((item, index) => {
      return (
        <>
          <tbody>
            <tr>
              <td scope="row">{index + 1}</td>
              <td>
                <div
                  style={{
                    width: "300px",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                  }}
                >
                  {" "}
                  {item.session_level} | {item.Topic__c}
                </div>
              </td>
              <td>
                <div style={{ width: "120px" }}>
                  <label style={{ display: "inherit", color: "grey" }}>
                    {DisplayDay(item.Date__c)}
                  </label>
                  {DisplayDate(item.Date__c)}
                </div>
              </td>

              {/* <td>{DisplayLocalTime(item.Batch_Local_Time__c)}</td> */}

              <td>
                {/* <a href="https://zoom.us/" target="_blank"> */}
                <div style={{ width: "100px" }}>
                  {item.Session_Status__c == "Planned" &&
                  item.Session_Type__c == "Training" ? (
                    <>{DisplayLocalTime(item.Batch_Local_Time__c)}</>
                  ) : item.Session_Type__c == "Training" &&
                    item.Session_Status__c == "Completed" ? (
                    <>{DisplayLocalTime(item.Batch_Local_Time__c)}</>
                  ) : (
                    <></>
                  )}
                </div>
              </td>

              {/*  */}
              {/* <td>{item.Class_Points__c}/10 </td> */}
              <td>
                {/* <a href="https://zoom.us/" target="_blank"> */}
                <div style={{ width: "100px" }}>
                  {item.Session_Status__c == "Planned" &&
                  item.Session_Type__c == "Training" ? (
                    <>{item.Class_Points__c}/10</>
                  ) : item.Session_Type__c == "Training" &&
                    item.Session_Status__c == "Completed" ? (
                    <>{item.Class_Points__c}/10</>
                  ) : (
                    <></>
                  )}
                </div>
              </td>
              <td>
                <div style={{ width: "100px" }}>
                  {item.Session_Status__c == "Completed" ? (
                    <span class="material-icons" style={{ color: "Green" }}>
                      circle
                    </span>
                  ) : (
                    <>
                      <span
                      // class="material-icons"

                      // style={{ color: "yellow" }}
                      >
                        <img
                          src="/images/spiner.gif"
                          style={{ width: 35 }}
                          onMouseEnter={handlePopoverOpen}
                          onMouseLeave={handlePopoverClose}
                        />
                      </span>
                      <Popover
                        // style={{
                        //   boxShadow: "1px 2px red",
                        // }}
                        id="mouse-over-popover"
                        sx={{
                          pointerEvents: "none",
                          // boxShadow: "1px 2px red",
                          // backgroundColor: "pink",
                        }}
                        open={open}
                        anchorEl={anchorEl}
                        anchorOrigin={{
                          vertical: "bottom",
                          horizontal: "left",
                        }}
                        transformOrigin={{
                          vertical: "top",
                          horizontal: "left",
                        }}
                        onClose={handlePopoverClose}
                        disableRestoreFocus
                      >
                        <Typography sx={{ p: 0.8 }}>
                          Upcoming sessions
                        </Typography>
                      </Popover>
                    </>
                  )}
                </div>
              </td>
              <td>
                {/* <a href="https://zoom.us/" target="_blank"> */}
                <div style={{ width: "150px" }}>
                  {item.Session_Status__c == "Planned" &&
                  item.Session_Type__c == "Training" ? (
                    <>
                      {/* <a href={item.Zoom_link1__c} target="_blank"> */}{" "}
                      {/* <button
                        class="btn buttonB"
                        // onClick={() => GoToZoomPage(item, index)}
                      >
                        <a href="https://zoom.us/">Join</a>
                      </button> */}
                      <a href={item.Zoom_link1__c} target="_blank">
                        <button
                          class="btn buttonB"
                          // onClick={() => GoToZoomPage(tomorrowDateData)}
                        >
                          {" "}
                          Join
                        </button>
                      </a>
                      {/* </a> */}
                    </>
                  ) : item.Session_Type__c == "Training" &&
                    item.Session_Status__c == "Completed" ? (
                    <>
                      <a>
                        {" "}
                        <button
                          class="btn buttonB"
                          style={{ backgroundColor: "grey", color: "white" }}
                        >
                          {" "}
                          Completed
                        </button>
                      </a>
                    </>
                  ) : (
                    <a>
                      {" "}
                      <button
                        class="btn buttonB"
                        style={{ backgroundColor: "grey", color: "white" }}
                      >
                        {" "}
                        Practical
                      </button>
                    </a>
                  )}
                </div>
              </td>
            </tr>
          </tbody>
        </>
      );
    });
  };

  const fillDataUpcoming = () => {
    // const LocalT = new Date();
    // return getList.map((item, index) => {
    return (
      <>
        <tbody>
          <tr>
            <td scope="row"> 1</td>
            <td>
              <div
                style={{
                  width: "300px",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                }}
              >
                {" "}
                {tomorrowDateData.session_level} | {tomorrowDateData.Topic__c}
              </div>
            </td>
            <td>
              <div style={{ width: "120px" }}>
                <label style={{ display: "inherit", color: "grey" }}>
                  {DisplayDay(tomorrowDateData.Date__c)}
                </label>
                {DisplayDate(tomorrowDateData.Date__c)}
              </div>
            </td>

            {/* <td>{DisplayLocalTime(tomorrowDateData.Batch_Local_Time__c)}</td> */}

            <td>
              {/* <a href="https://zoom.us/" target="_blank"> */}
              <div style={{ width: "100px" }}>
                {tomorrowDateData.Session_Status__c == "Planned" &&
                tomorrowDateData.Session_Type__c == "Training" ? (
                  <>{DisplayLocalTime(tomorrowDateData.Batch_Local_Time__c)}</>
                ) : tomorrowDateData.Session_Type__c == "Training" &&
                  tomorrowDateData.Session_Status__c == "Completed" ? (
                  <>{DisplayLocalTime(tomorrowDateData.Batch_Local_Time__c)}</>
                ) : (
                  <></>
                )}
              </div>
            </td>

            {/*  */}
            {/* <td>{tomorrowDateData.Class_Points__c}/10 </td> */}
            <td>
              {/* <a href="https://zoom.us/" target="_blank"> */}
              <div style={{ width: "100px" }}>
                {tomorrowDateData.Session_Status__c == "Planned" &&
                tomorrowDateData.Session_Type__c == "Training" ? (
                  <>{tomorrowDateData.Class_Points__c}/10</>
                ) : tomorrowDateData.Session_Type__c == "Training" &&
                  tomorrowDateData.Session_Status__c == "Completed" ? (
                  <>{tomorrowDateData.Class_Points__c}/10</>
                ) : (
                  <></>
                )}
              </div>
            </td>
            <td>
              <div style={{ width: "100px" }}>
                {tomorrowDateData.Session_Status__c == "Completed" ? (
                  <span class="material-icons" style={{ color: "Green" }}>
                    circle
                  </span>
                ) : (
                  <>
                    <span
                    // class="material-icons"

                    // style={{ color: "yellow" }}
                    >
                      <img
                        src="/images/spiner.gif"
                        style={{ width: 35 }}
                        onMouseEnter={handlePopoverOpen}
                        onMouseLeave={handlePopoverClose}
                      />
                    </span>
                    <Popover
                      // style={{
                      //   boxShadow: "1px 2px red",
                      // }}
                      id="mouse-over-popover"
                      sx={{
                        pointerEvents: "none",
                        // boxShadow: "1px 2px red",
                        // backgroundColor: "pink",
                      }}
                      open={open}
                      anchorEl={anchorEl}
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "left",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "left",
                      }}
                      onClose={handlePopoverClose}
                      disableRestoreFocus
                    >
                      <Typography sx={{ p: 0.8 }}>Upcoming sessions</Typography>
                    </Popover>
                  </>
                )}
              </div>
            </td>
            <td>
              {/* <a href="https://zoom.us/" target="_blank"> */}
              <div style={{ width: "150px" }}>
                {tomorrowDateData.Session_Status__c == "Planned" &&
                tomorrowDateData.Session_Type__c == "Training" ? (
                  <>
                    {/* <a href={tomorrowDateData.Zoom_link1__c} target="_blank"> */}{" "}
                    <a href={tomorrowDateData.Zoom_link1__c} target="_blank">
                      <button
                        class="btn buttonB"
                        // onClick={() => GoToZoomPage(tomorrowDateData)}
                      >
                        {" "}
                        Join
                      </button>
                    </a>
                  </>
                ) : tomorrowDateData.Session_Type__c == "Training" &&
                  tomorrowDateData.Session_Status__c == "Completed" ? (
                  <>
                    <a>
                      {" "}
                      <button
                        class="btn buttonB"
                        style={{ backgroundColor: "grey", color: "white" }}
                      >
                        {" "}
                        Completed
                      </button>
                    </a>
                  </>
                ) : (
                  <a>
                    {" "}
                    <button
                      class="btn buttonB"
                      style={{ backgroundColor: "grey", color: "white" }}
                    >
                      {" "}
                      Practical
                    </button>
                  </a>
                )}
              </div>
            </td>
          </tr>
        </tbody>
      </>
    );
    // });
  };

  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              // class="welcome-area"
              style={{
                backgroundColor: "white",
                width: "100%",
              }}
            >
              <Header history={props.history} />
            </div>
          </div>
        </nav>
        {/* {fillData()} */}
        {loading ? (
          <center>
            <img src="/images/LLyJ.gif" style={{ marginTop: "15%" }} />
          </center>
        ) : (
          <div class="mt-5" style={{ padding: "25px" }}>
            <div class="heading">
              <b>
                {getLevel} | {getClass_Type}
              </b>
              <span
                class="batchId ml-4"
                onClick={() => history.push({ pathname: "/ClassDuration" })}
                style={{ cursor: "pointer" }}
              >
                BATCH ID: {getBatchId}{" "}
                <i
                  class="bi bi-exclamation-circle"
                  onMouseEnter={handlePopoverOpen2}
                  onMouseLeave={handlePopoverClose2}
                ></i>
                <Popover
                  // style={{
                  //   boxShadow: "1px 2px red",
                  // }}
                  id="mouse-over-popover"
                  sx={{
                    pointerEvents: "none",
                    // boxShadow: "1px 2px red",
                    // backgroundColor: "pink",
                  }}
                  open={open2}
                  anchorEl={anchorEl2}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  transformOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                  }}
                  onClose={handlePopoverClose2}
                  disableRestoreFocus
                >
                  <Typography
                    sx={{
                      p: 0.8,
                      fontSize: "12px",
                      borderRadius: "50px",
                      padding: "15px 15px 5px 15px",
                    }}
                  >
                    <span> BATCH ID : {getBatchId}</span>
                    <br />
                    <br />
                    Click here for batch <br />
                    and level detail
                  </Typography>
                </Popover>{" "}
              </span>

              <span
                class="stuId"
                onClick={() => history.push({ pathname: "/StudentProfile" })}
                style={{ cursor: "pointer" }}
              >
                STUDENT ID: {getStudentId}{" "}
                <i
                  class="bi bi-exclamation-circle"
                  onMouseEnter={handlePopoverOpen3}
                  onMouseLeave={handlePopoverClose3}
                ></i>
                <Popover
                  // style={{
                  //   boxShadow: "1px 2px red",
                  // }}
                  id="mouse-over-popover"
                  sx={{
                    pointerEvents: "none",
                    // boxShadow: "1px 2px red",
                    // backgroundColor: "pink",
                  }}
                  open={open3}
                  anchorEl={anchorEl3}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  transformOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                  }}
                  onClose={handlePopoverClose3}
                  disableRestoreFocus
                >
                  <Typography
                    sx={{
                      p: 0.8,
                      fontSize: "12px",
                      borderRadius: "50px",
                      padding: "15px 15px 5px 15px",
                    }}
                  >
                    <span> STUDENT ID : {getStudentId}</span>
                    <br />
                    <br />
                    Click here for more <br />
                    detail
                  </Typography>
                </Popover>{" "}
              </span>

              <span
                class="clsDur ml-4"
                onClick={() => history.push({ pathname: "/ClassDuration" })}
                style={{ cursor: "pointer" }}
              >
                Class Duration{" "}
              </span>

              <span
                class="hrMin"
                style={{
                  backgroundColor: "#f9f9f9",
                  width: 100,
                  display: "inline-grid",
                }}
              >
                {" "}
                {getClassDuration}{" "}
              </span>
            </div>
            <div
              class="heading"
              style={{
                margin: "0px",
                backgroundColor: "#f9f9f9",
                width: "fit-content",
                height: "42px",
                padding: "10px",
                marginTop: "20px",
              }}
            >
              <b style={{ fontSize: "13px", fontWeight: "700" }}>JOIN CLASS</b>
            </div>
            {/* First - Table */}
            {/* {tomorrowDate ? ( */}
            <div>
              <table class="table table-borderless m-2">
                <thead class="" style={{ backgroundColor: "#f9f9f9" }}>
                  <tr style={{ color: "#738097" }}>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      No.
                    </th>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      TOPIC
                    </th>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      DATE
                    </th>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      TIME
                    </th>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      CLASS POINTS
                    </th>
                    <th
                      scope="col"
                      style={{ fontSize: "13px", fontWeight: "700" }}
                    >
                      STATUS
                    </th>
                    <th
                      scope="col"
                      style={{
                        fontSize: "13px",
                        fontWeight: "700",
                        textAlign: "center",
                      }}
                    >
                      LINK
                    </th>
                  </tr>
                </thead>
                {/* {CheckTommorowSession()} */}
                {fillDataUpcoming()}
              </table>
            </div>
            {/* )  */}
            {/* // : ( */}
            <>{/* Nothing */}</>
            {/* // )} */}
            {/* Second - Table */}
            <table class="table table-borderless m-2">
              <thead class="" style={{ backgroundColor: "#f9f9f9" }}>
                <tr style={{ color: "#738097" }}>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    No.
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    TOPIC
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    DATE
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    TIME
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    CLASS POINTS
                  </th>
                  <th
                    scope="col"
                    style={{ fontSize: "13px", fontWeight: "700" }}
                  >
                    STATUS
                  </th>
                  <th
                    scope="col"
                    style={{
                      fontSize: "13px",
                      fontWeight: "700",
                      textAlign: "center",
                    }}
                  >
                    LINK
                  </th>
                </tr>
              </thead>
              {!checkCon ? (
                fillData()
              ) : (
                <p
                  style={{
                    position: "absolute",
                    left: "50%",
                    top: "60%",
                  }}
                >
                  <b>{NoBatch} </b>
                </p>
              )}
            </table>
            {/* )} */}
          </div>
        )}
      </div>
    </>
  );
}
