import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Login from "./admin/Login";
import Batches from "./dashboard/Batches";
import Dashboard from "./dashboard/Dashboard";
import Header from "./dashboard/Header";
import StudentProfile from "./usericon/StudentProfile";
import ClassDuration from "./dashboard/ClassDuration";
import CheckPayment from "./dashboard/CheckPayment";
import EmptyCart from "./dashboard/EmptyCart";
import Notifications from "./dashboard/Notifications";
import ListItems from "./ListItems";
import Myhomeworks from "./dashboard/Myhomework";
import Myreviews from "./dashboard/Myreviews";
import Referandearn from "./dashboard/Referandearn";
import Mygames from "./dashboard/Mygames";
import Tournaments from "./dashboard/Tournaments";
import MyCart from "./dashboard/MyCart";
import MyCourse from "./usericon/MyCourse";
import PurchaseHistory from "./usericon/PurchaseHistory";
import HelpAndSupport from "./usericon/HelpAndSupport";
import NotiDetails from "./dashboard/NotiDetails";
import PageNotFound from "./dashboard/PageNotFound";
import ZoomView from "./dashboard/zoomView";

export default function AdminRouter(props) {
  return (
    <>
      <Router>
        {/* <Routes> */}

        <Route
          exact
          strict
          component={Login}
          path="/"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Dashboard}
          path="/Dashboard"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Login}
          path="/Login"
          history={props.history}
        />

        <Route
          exact
          strict
          component={StudentProfile}
          path="/StudentProfile"
          history={props.history}
        />

        <Route
          exact
          strict
          component={ClassDuration}
          path="/ClassDuration"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Batches}
          path="/Batches"
          history={props.history}
        />
        <Route
          exact
          strict
          component={Myhomeworks}
          path="/My Homework"
          history={props.history}
        />
        <Route
          exact
          strict
          component={Myreviews}
          path="/My Reviews"
          history={props.history}
        />
        <Route
          exact
          strict
          component={ListItems}
          path="/ListItems"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Referandearn}
          path="/Refer and earn"
          history={props.history}
        />

        <Route
          exact
          strict
          component={CheckPayment}
          path="/CheckPayment"
          history={props.history}
        />

        <Route
          exact
          strict
          component={ZoomView}
          path="/ZoomView"
          history={props.history}
        />

        <Route
          exact
          strict
          component={EmptyCart}
          path="/EmptyCart"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Notifications}
          path="/Noti"
          history={props.history}
        />

        <Route
          exact
          strict
          component={NotiDetails}
          path="/NotiDetails"
          history={props.history}
        />

        <Route
          exact
          strict
          component={Mygames}
          path="/My Games"
          history={props.history}
        />

        <Route
          exact
          strict
          component={PageNotFound}
          // component={Tournaments}
          path="/Tournaments"
          history={props.history}
        />

        <Route
          exact
          strict
          component={MyCart}
          path="/My Cart"
          history={props.history}
        />

        <Route
          exact
          strict
          component={MyCourse}
          path="/My Course"
          history={props.history}
        />

        <Route
          exact
          strict
          component={PurchaseHistory}
          path="/Purchase History"
          history={props.history}
        />

        <Route
          exact
          strict
          component={HelpAndSupport}
          path="/Help and Support"
          history={props.history}
        />
      </Router>
    </>
  );
}
