const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  "& .MuiDialogContent-root": {
    padding: theme.spacing(2),
  },
  "& .MuiDialogActions-root": {
    padding: theme.spacing(1),
  },
  "& .css-1t1j96h-MuiPaper-root-MuiDialog-paper": {
    backgroundColor: "#fff",
    // color: rgba(0, 0, 0, 0.87),
    // webkitTransition: boxShadow '300ms' ,cubicBezier(0.4, 0, 0.2, 1) 0ms,
    // transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    borderRadius: "4px",
    // box-shadow: 0px 11px 15px -7px rgb(0 0 0 / 20%), 0px 24px 38px 3px rgb(0 0 0 / 14%), 0px 9px 46px 8px rgb(0 0 0 / 12%),
    // margin: "32px",
    position: "relative",
    overflowY: "inherit",
    display: "-webkit - box",
    display: "-webkit - flex",
    display: "-ms - flexbox",
    display: "flex",
    maxWidth: "620px",
    right: 90,
    // -webkit-flex-direction: column,
    // -ms-flex-direction: column,
    // flex-direction: column,
  },
  "& .css-uhb5lp": {
    backgroundColor: "rgb(255, 255, 255)",
    color: "rgba(0, 0, 0, 0.87)",
    // transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    borderRadius: "4px",
    // boxShadow: rgb(0 0 0 / 20%) 0px 11px 15px -7px, rgb(0 0 0 / 14%) 0px 24px 38px 3px, rgb(0 0 0 / 12%) 0px 9px 46px 8px;
    margin: "32px",
    position: "relative",
    /* overflow-y: auto; */
    display: "flex",
    flexDirection: "column",
    /* max-height: calc(100% - 64px); */
    /* max-width: 600px; */
  },
  "& .css-8wbvxb .css-uhb5lp": {
    backgroundColor: "rgb(255, 255, 255)",
    color: "rgba(0, 0, 0, 0.87)",
    borderRadius: "4px",
    /* margin: 32px; */
    position: "relative",
    display: "flex",
    flexDirection: "column",
    maxWidth: "fit-content",
    overflow: "hidden",
  },
  // }
}));

const BootstrapDialogTitle = (props) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

BootstrapDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

//
export default function ClassDuration(props) {
  const [getList, setList] = useState([]);
  const [getOpen, setOpen] = React.useState(false);
  // const { width, height } = useWindowSize();

  var Account = JSON.parse(localStorage.getItem("Account"));
  var username = Account.Name;

  const chkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      props.history.replace({ pathname: "/Login" });
      // setLoading(false);
    } else {
      props.history.replace({ pathname: "/ClassDuration" });
      // setLoading(false);
    }
  };

  const handleClickOpen = (item) => {
    setOpen(true);
    setConfiOpen("xyz");
    setLevell(item.Level);
    setEndDatee(item.EndDate);
  };
  const handleClose = () => {
    setOpen(false);
  };

  ////certi

  const today = new Date();
  const tomorrow = new Date();
  const nextTomorrow = new Date();

  tomorrow.setDate(tomorrow.getDate() + 1);
  nextTomorrow.setDate(nextTomorrow.getDate() - 5);

  const [getPastLevels, setPastLevels] = useState([]);
  const [values, setValues] = useState([today, tomorrow, nextTomorrow]);
  const [upcomingLevel, setUpcomingLevel] = useState("");
  const [anchorEl, setAnchorEl] = React.useState(null);

  const [NoAttendance, setNoAttendance] = useState("");
  const [checkConAtten, setCheckConAtten] = useState(false);

  const [NoPastLevel, setNoPastLevel] = useState("");
  const [checkConLevel, setCheckConLevel] = useState(false);
  const [getConfiOpen, setConfiOpen] = useState("");

  const [getLevell, setLevell] = useState("");
  const [GetEndDatee, setEndDatee] = useState("");

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handlePopoverClose = () => {
    setAnchorEl(null);
  };
  const openTooltip = Boolean(anchorEl);
  ///
  const DisplayDate = (Coming_date) => {
    var newDate = new Date(Coming_date);
    // console.log("newDate===", newDate);
    var dd = newDate.getDate();
    // console.log("dd=====", dd);
    var mm = newDate.toLocaleString("default", { month: "long" });
    // console.log("mm=====", mm);
    var yy = newDate.getFullYear();
    // console.log("yy=======", yy);

    var nameday = new Date(newDate).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);

    var finalDate = `${dd}  ${mm} ${yy}`;
    // console.log("finalDate", finalDate);
    return finalDate;
  };
  const DisplayMonth = (Coming_date) => {
    var newMonth = new Date(Coming_date);
    var mm = newMonth.toLocaleString("default", { month: "long" });
    var yy = newMonth.getFullYear();
    var final_Month = `${mm} ${yy}`;
    return final_Month;
  };
  const DisplayDay = (Coming_date) => {
    var newDay = new Date(Coming_date);

    var nameday = new Date(newDay).toLocaleString("en-us", {
      weekday: "long",
    });
    // console.log("nameday=======", nameday);
    return nameday;
  };
  ///

  const fetchData = async (e_id, idd) => {
    // console.log("e_iddddddddddd++++===", e_id, idd);
    let homeworkResult = await postData(
      "Lead/AttendenceShow",
      // { email: e_id }
      { id: idd }
    );

    if (homeworkResult.result != 0) {
      let myLevel = homeworkResult.result[0].Level__c;
      var l_aa = [];
      l_aa.push(myLevel);
      console.log("l_aa==================187", l_aa);
      let l_level = l_aa[0].split(";");
      var final_level = l_level[l_level.length - 1];

      console.log("final_level===============191", final_level);
      if (final_level == "Beginner") {
        setUpcomingLevel("Advanced Beginner");
      } else if (final_level == "Advanced Beginner") {
        setUpcomingLevel("Intermediate");
      } else if (final_level == "Intermediate") {
        setUpcomingLevel("Advanced Part 1");
      } else if (final_level == "Advanced Part 1") {
        setUpcomingLevel("Advanced Part 2");
      } else {
        setUpcomingLevel("");
      }
    } else {
      setUpcomingLevel("No upcoming level");
      // setNoAttendance("No attendance record...");
    }

    // setUpcomingLevel(homeworkResult.result[0].stu_level);
    const li = homeworkResult.result.map((i) => [
      new Date(i.Date__c),
      i.Attendance_show,
    ]);
    // console.log("li=============", li);
    setList(li);
    // console.log("AttendenceShow=====", homeworkResult.result);
  };

  const fetchPastLevels = async (em_id, idd) => {
    let PastLevelsResult = await postData("BatchApi/PastLevelsDisplay", {
      email: em_id,
      id: idd,
    });
    // console.log("PastLevelsResult=======", PastLevelsResult.result);
    if (PastLevelsResult.result.length != 0) {
      setPastLevels(PastLevelsResult.result);
    } else {
      setCheckConLevel(true);
      setNoPastLevel("No Past Levels...");
    }
  };

  const showCelebration = () => {
    return (
      <>
        {getOpen == "true" ? <Confetti width={"550%"} height={"400%"} /> : ""}
      </>
    );
  };
  const showCertificate = () => {
    return (
      <>
        {/* {getOpen ? <Confetti height={"2000%"} gravity={0.5} /> : ""} */}
        <div>
          <BootstrapDialog
            style={{
              // height: "100%",
              // width: "100%",
              zIndex: 9999,
              overflowY: "hidden",
            }}
            onClose={handleClose}
            aria-labelledby="customized-dialog-title"
            open={getOpen}
          >
            <AppBar>
              <Confetti gravity={0.2} />
            </AppBar>
            {getLevell == "Beginner" ? (
              <img
                style={{
                  width: "130%",
                  height: 700,
                  overflowY: "hidden",
                  maxWidth: "200%",
                }}
                src="/images/Beginner.png"
              />
            ) : (
              ""
            )}

            {getLevell == "Intermediate" ? (
              <img
                style={{
                  width: "130%",
                  height: 700,
                  overflowY: "hidden",
                  maxWidth: "200%",
                }}
                src="/images/intermediate.png"
              />
            ) : (
              ""
            )}

            {getLevell == "Advanced Beginner" ? (
              <img
                // class="img-responsive"
                style={{
                  width: "130%",
                  height: 700,
                  overflowY: "hidden",
                  maxWidth: "200%",
                }}
                src="/images/Advanced_Beginner.png"
              />
            ) : (
              ""
            )}

            {getLevell == "Advanced Part 1" ? (
              <img
                style={{
                  width: "130%",
                  height: 700,
                  overflowY: "hidden",
                  maxWidth: "200%",
                }}
                src="/images/AdvancedPart1-certi.png"
              />
            ) : (
              ""
            )}

            {getLevell == "Advanced Part 2" ? (
              <img
                style={{
                  width: "130%",
                  height: 700,
                  overflowY: "hidden",
                  maxWidth: "200%",
                }}
                src="/images/AdvancedPart1-certi.png"
              />
            ) : (
              ""
            )}

            {getLevell == "Beginner" || getLevell == "Advanced Beginner" ? (
              <div
                style={{
                  color: "blue",
                  position: "absolute",
                  top: "41%",
                  left: "52%",
                  fontFamily: "Monotype Corsiva",
                  fontSize: 48,
                }}
              >
                {username}
              </div>
            ) : (
              <div
                style={{
                  color: "blue",
                  position: "absolute",
                  top: "41%",
                  left: "52%",
                  fontFamily: "Monotype Corsiva",
                  fontSize: 48,
                }}
              >
                {username}
              </div>
            )}

            <div
              style={{
                // color: "white",
                position: "absolute",
                top: "47%",
                left: "24%",
              }}
            >
              {getLevell == "Beginner" || getLevell == "Advanced Beginner" ? (
                <div
                  style={{
                    color: "blue",
                    fontSize: "16px",
                    fontFamily: "Arial",

                    textAlign: "center",
                  }}
                >
                  <p style={{ width: "609px", marginTop: "40px" }}>
                    learning with Upstep Academy has successfully completed
                    ONLINE
                  </p>
                  <p>
                    {getLevell} Chess Level in{" "}
                    <b>{DisplayMonth(GetEndDatee)}</b>
                  </p>
                </div>
              ) : (
                <div
                  style={{
                    color: "white",
                    fontSize: "16px",
                    fontFamily: "Arial",
                  }}
                >
                  <p style={{ width: "609px", marginTop: "40px" }}>
                    learning with Upstep Academy has successfully completed
                    ONLINE
                  </p>
                  <p>
                    {getLevell} Chess Level in{" "}
                    <b>{DisplayMonth(GetEndDatee)}</b>
                  </p>
                </div>
              )}
            </div>
            {/* )} */}
          </BootstrapDialog>
        </div>
      </>
    );
  };

  const showPastLevels = () => {
    return getPastLevels.map((item, index) => {
      // console.log("itemmmm==========", item);
      return (
        <>
          <tbody>
            <tr>
              <td scope="row">{index + 1}</td>
              <td>{item.Level}</td>
              <td>
                <label style={{ display: "inherit", color: "grey" }}>
                  {/* {DisplayDay(item.EnrollmentDate)} */}
                </label>
                {item.StartDate}
                {/* {DisplayDate(item.EnrollmentDate)} */}
              </td>
              <td>
                <label style={{ display: "inherit", color: "grey" }}>
                  {/* {DisplayDay(item.EndDate)} */}
                </label>
                {/* {DisplayDate(item.EndDate)} */}
                {item.EndDate}
              </td>
              <td>{item.Score}/300</td>
              {item.Attendance__c >= 75 ? (
                <td style={{ color: "#44bd32" }}>{item.Attendance}%</td>
              ) : (
                <td style={{ color: "#e84118" }}>{item.Attendance}%</td>
              )}
              <td>
                <button
                  class="buttonB"
                  // onClick={openImg}
                  // onClick={() => handleClickOpen()}
                  onClick={() => handleClickOpen(item)}
                >
                  {" "}
                  Open
                </button>
              </td>
            </tr>
          </tbody>
        </>
      );
    });
  };

  useEffect(() => {
    chkToken();
    var user = JSON.parse(localStorage.getItem("User"));
    var e_id = user.email;
    var idd = user.id;
    fetchData(e_id, idd);
    fetchPastLevels(e_id, idd);
  }, []);

  return (
    <>
      <div class="sidemenu-area">
        <ListItems />
      </div>

      <div class="main-content d-flex flex-column">
        <nav class="navbar top-navbar navbar-expand">
          <div class="collapse navbar-collapse" id="navbarSupportContent">
            <div class="responsive-burger-menu d-block d-lg-none">
              <span class="top-bar"></span>
              <span class="middle-bar"></span>
              <span class="bottom-bar"></span>
            </div>
            <div class="navbar-nav left-nav align-items-center"></div>

            <div
              // class="welcome-area"
              style={{
                backgroundColor: "white",
                width: "100%",
              }}
            >
              <Header history={props.history} />
            </div>
          </div>
        </nav>
        <div class="row">
          <div class="card col-8" style={{ padding: "12px" }}>
            <CurrentBatch />
          </div>

          <div class="card col-4">
            <div class="col">
              <div
                class="card mt-3"
                style={{ backgroundColor: "#ecf0f1", borderRadius: "5px" }}
              >
                <div>
                  <b>ATTENDANCE</b>
                </div>
              </div>
              <div class="card" style={{ width: "100%" }}>
                <div class="row">
                  <Calendar
                    readOnly
                    mapDays={({ date }) => {
                      var present = [];
                      var absent = [];
                      var Planned = [];
                      // var planned = [];
                      getList.map((item) => {
                        if (
                          item[0].getDate() == date.day &&
                          item[0].getMonth() + 1 == date.month &&
                          item[1].toLowerCase() == "Present".toLowerCase()
                        ) {
                          // console.log(
                          //   "dataeeeeee " + item[0].getDate() + ", " + date.day
                          // );
                          present.push(date.day);
                        } else if (
                          item[0].getDate() == date.day &&
                          item[0].getMonth() + 1 == date.month &&
                          item[1].toLowerCase() == "absent".toLowerCase()
                        ) {
                          // console.log(
                          //   "dataeeeeee " + item[0].getDate() + ", " + date.day
                          // );
                          absent.push(date.day);
                        } else if (
                          item[0].getDate() == date.day &&
                          item[0].getMonth() + 1 == date.month &&
                          item[1].toLowerCase() == "Planned".toLowerCase()
                        ) {
                          // console.log(
                          //   "dataeeeeee " + item[0].getDate() + ", " + date.day
                          // );
                          <Popover
                            style={{
                              boxShadow: "1px 2px red",
                            }}
                            id="mouse-over-popover"
                            sx={{
                              pointerEvents: "none",
                            }}
                            open={openTooltip}
                            anchorEl={anchorEl}
                            anchorOrigin={{
                              vertical: "bottom",
                              horizontal: "left",
                            }}
                            transformOrigin={{
                              vertical: "top",
                              horizontal: "left",
                            }}
                            onClose={handlePopoverClose}
                            disableRestoreFocus
                          >
                            <Typography sx={{ p: 0.8 }}>
                              Planned sessions
                            </Typography>
                          </Popover>;
                          Planned.push(date.day);
                        }
                      });
                      // console.log("Present===new===", present);
                      // console.log({ present });

                      if (present.includes(date.day)) {
                        return {
                          style: {
                            color: "white",
                            backgroundColor: "#2ecc71",
                          },
                        };
                      } else if (absent.includes(date.day)) {
                        return {
                          style: {
                            color: "white",
                            backgroundColor: "#eb2f06",
                          },
                        };
                      } else if (Planned.includes(date.day)) {
                        return {
                          style: {
                            color: "black",
                            backgroundColor: "#BDC581",
                          },
                        };
                      }
                    }}
                    onMouseEnter={handlePopoverOpen}
                    onMouseLeave={handlePopoverClose}
                  />
                  {/* {NoAttendance} */}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div
            class="card mt-4 heading"
            style={{ backgroundColor: "#ecf0f1", borderRadius: "5px" }}
          >
            <b>UPCOMING LEVEL</b>
          </div>
          <div class="card heading" style={{ padding: "1%" }}>
            <table
              class="table table-borderless m-2"
              style={{ display: "flex" }}
            >
              <thead class="">
                <tr style={{ fontSize: "12px", justifyContent: "center" }}></tr>
              </thead>
              <tbody>
                <tr>
                  {/* <td scope="row">01</td> */}
                  <td scope="row heading">{upcomingLevel}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div>
          <div
            class="card mt-4 heading"
            style={{ backgroundColor: "#ecf0f1", borderRadius: "5px" }}
          >
            <b>PAST LEVEL</b>
          </div>
          <div class="card heading" style={{ padding: "1%" }}>
            <table class="table table-borderless m-2">
              <thead class="thead-light">
                <tr style={{ fontSize: "11px", justifyContent: "center" }}>
                  <th scope="col">SR.No</th>
                  <th scope="col">LEVEL</th>
                  <th scope="col">START DATE</th>
                  <th scope="col">END DATE</th>
                  <th scope="col">ASSESSMENT SCORE</th>
                  <th scope="col">ATTENDANCE</th>
                  <th scope="col">CERTIFICATE</th>
                </tr>
              </thead>
              {checkConLevel ? (
                <p
                  style={{ position: "relative", left: "220%", bottom: "50%" }}
                >
                  {" "}
                  {NoPastLevel}{" "}
                </p>
              ) : (
                showPastLevels()
              )}
            </table>
          </div>
        </div>
        {showCertificate()}
      </div>
    </>
  );
}
