import React from "react";
import { adminRoutes } from "../components/config/MenuItems";
import { useHistory } from "react-router-dom";

export default function ListItems(props) {
  // console.log("propssssss=>>>>", props);
  var history = useHistory();

  const handleGo = (data) => {
    // console.log("data.name===>>>", data.name);
    // console.log("data.component===>>>", data.component);
    history.push({ pathname: `${data.name}` });
  };

  const MenuItemss = () => {
    return (
      <div class="sidemenu-body">
        <ul
          class="sidemenu-nav metisMenu h-100"
          id="sidemenu-nav"
          style={{ backgroundColor: "white" }}
        >
          {adminRoutes.map((data) => (
            <li class="nav-item" style={{ cursor: "pointer" }}>
              {/* <span class="nav-link">
                <span class="icon" style={{ marginRight: "20px" }}>
                  <i class={data.icon} style={{ color: "#18a0fb" }}></i>{" "}
                </span>

                <span class="menu-title ac" onClick={() => handleGo(data)}>
                  {data.name}
                </span>
              </span> */}
              <span
                onClick={() => handleGo(data)}
                class="nav-link"
                style={{ marginBottom: "4px" }}
              >
                <span class="icon" style={{ marginRight: "20px" }}>
                  <i class={data.icon} style={{ color: "#18a0fb" }}></i>
                </span>
                <span class="menu-title">{data.name}</span>
              </span>
            </li>
          ))}
        </ul>
      </div>
    );
  };

  return (
    <>
      <div class="sidemenu-area">
        <div class="sidemenu-header">
          <span
            // href="dashboard-ecommerce.html"
            class="navbar-brand d-flex align-items-center"
          >
            <img src="/images/upstepLogo.png" alt="image" />
          </span>

          {/* <div class="responsive-burger-menu d-block d-lg-none">
            <span class="top-bar"></span>
            <span class="middle-bar"></span>
            <span class="bottom-bar"></span>
          </div> */}
        </div>
        {MenuItemss()}
      </div>
    </>
  );
}
